
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

































































































.board {
  background-color: #333399;
  margin: 3vh;
  color: white;
  border: 1px solid;
  padding: 10px;
  -webkit-box-shadow: 5px 10px #14143b;
          box-shadow: 5px 10px #14143b;
}


































































































































































.list {
  background-color: #333399;
  margin: 3vh;
  color: white;
  border: 1px solid;
  padding: 10px;
  -webkit-box-shadow: 5px 10px #14143b;
          box-shadow: 5px 10px #14143b;
}
.dragArea {
  min-height: 3vh
}

.task {
  display: block;
}
.task:hover .task-comment {
  display: block;
}
.task-comment {
  display: none;
}
